/* App.css */

* {
  margin: 0;
  padding: 0;
}

body {
  font-family: "맑은 고딕", arial, sans-serif;
  color: #333;
}

.header {
  background: #00f;
  text-align: center;
}

.header>h1 {
  color: #fff;
  font-size: 30px;
}

/* 메인 서식 */
main {
  padding: 15px 10px;
}

.list01 {
  margin: 10px 0 15px 30px;
}

.list01 ul,
.list01 li ol {
  margin-left: 16px;
  background: #ececec;
}

main .title2 {
  color: rgb(255, 35, 35);
  line-height: 30px;
  margin: 14px 0;
}

main .title3 {
  color: #ff4ebb;
  line-height: 30px;
  margin: 10px 0;
}

main .title4{
  line-height:50px;
}

.footer {
  text-align: center;
  line-height: 40px;
  background: #333;
}

.footer>address {
  color: #fff;
  font-style: normal;
}


/* 컬리 예제 */
.kbox{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom:30px;
  padding:10px;
}

.kbox2{
  border:1px solid #ccc;
  width:120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.btn1{
  padding:10px;
  border:none;
  background: none;
  font-size: 20px;
  cursor: pointer;
}

.num1{
  font-weight:bold;
  font-size:20px;
}

.pbox{
  margin-top:30px;
  text-align: end;
}

.price{font-size:30px; font-weight:bold;}

.kinput{
  width:100%; height:60px;
  background-color: #5f008C;
  border-radius: 8px;
  color:#fff;
  border:none;
  font-size:20px;
  margin-top:20px;
}
.discount{
  text-decoration: line-through;
  color:#ccc;
}
.ktle{
  margin:30px 10px 20px 10px;
}
.kouter{
  width:600px;
  border:1px solid #ccc;
  padding:0 20px 20px 20px;
  margin-bottom:20px;
  margin-top:20px;
}